





































































































































































































































		import uniIcon from '../../components/uni-icon.vue'
	 
    import { mapState, mapMutations } from 'vuex'
    
		export default {
				components: {
					uniIcon
				},
				computed: {
						...mapState(['hasLogin', 'forcedLogin'])
				},
				methods: {
						...mapMutations(['logout']),
						bindLogin() {
								uni.navigateTo({
										url: '../login/login',
								});
						},
						bindLogout() {
								this.logout();
								/**
								 * 如果需要强制登录跳转回登录页面
								 */
								if (this.forcedLogin) {
										uni.reLaunch({
												url: '../login/login',
										});
								}
						}
				}
		}




